import React from "react";
import { AdsBlock } from "./general/style/style";

class AdsComp extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount () {
    try {
      typeof(adsOnLoadFunctions)!="undefined" && typeof(callAdScriptLoadAds)!="undefined" && adsOnLoadFunctions.push(() => callAdScriptLoadAds(this.props.adId, { zone: 'web', subSlots: this.props.subSlots, origin: "webitem", topicKey:this.props.topicKey}));
    } catch (err) {
      console.log(err);
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    return false;
  }

  render() {
    return <AdsBlock id={this.props.adId}></AdsBlock>;
  }
}
export default AdsComp;
